<template>
  <user>
    <html-head 
      title="Add Role"
    ></html-head>

    <page-header>
      <template v-slot:title >
        <span v-text="`Add Role`"></span>
      </template>
    </page-header>

    <v-row>
      <v-col cols="12" lg="6">
        <validation-observer ref="validation">
        <v-form ref="form" @submit.prevent="submit" enctype="multipart/form-data" :disabled="role.isFetching">
          <v-card elevation="0">
            <v-card-title v-text="`Add Role`"></v-card-title>
            <v-card-text>
              <form-errors :error="role.errors.message"></form-errors>
              <validation-provider vid="name" :name="'Name'" v-slot="{ errors }">
                <v-text-field
                  :error-messages="errors"
                  label="Name"
                  name="name"
                  outlined
                  v-model="role.data.name"
                  @input="role.prestine( false )"
                ></v-text-field>
              </validation-provider>
            </v-card-text>
            <v-card-actions class="d-flex justify-end"> 
              <v-btn color="primary" type="submit" :loading="role.isFetching" :disabled="role.isPrestine">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-form>
        </validation-observer>
      </v-col>
    </v-row>
  </user>
</template>

<script>
import Api from './routes/api'
import Role from './models/Role'
import { mapActions, mapGetters } from 'vuex'

export default {
  computed: {
    ...mapGetters({
      auth: 'auth/auth',
    }),
  },

  data: () => ({
    role: new Role,
  }),

  methods: {
    ...mapActions({
      setSnackbar: 'snackbar/set',
    }),

    async submit () {
      const data = await this.role.request({
        method: 'post',
        url: Api.main(),
        data: this.role.readyData( this.$refs.form.$el ),
      })
      
      if( !data ) 
        return this.$refs.validation.setErrors( this.role.errors.details )
        
      this.setSnackbar({
        model: true,
        text: 'Role created',
        timeout: 3000
      })

      this.$router.push({ name: 'roles.edit', params: { id: data._id } })
    },
  },

  mounted () {
    this.role.fetch( false )
    this.role.load( false )
  }
}
</script>